// Login component for accessing protected content
// Provides authentication interface with password change functionality

import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';

function Login({ onClose }) {
  const [credentials, setCredentials] = useState({ email: '', password: '' });
  const [error, setError] = useState('');
  const [showPasswordChange, setShowPasswordChange] = useState(false);
  const [passwordChangeData, setPasswordChangeData] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });
  const [passwordChangeError, setPasswordChangeError] = useState('');
  const [passwordChangeSuccess, setPasswordChangeSuccess] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  
  const { login, changePassword } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);
    
    const success = await login(credentials.email, credentials.password);
    setIsLoading(false);
    
    if (success) {
      onClose && onClose();
    } else {
      setError('Invalid email or password');
    }
  };

  const handlePasswordChange = async (e) => {
    e.preventDefault();
    setPasswordChangeError('');
    setPasswordChangeSuccess(false);
    setIsLoading(true);

    if (passwordChangeData.newPassword !== passwordChangeData.confirmPassword) {
      setPasswordChangeError('New passwords do not match');
      setIsLoading(false);
      return;
    }

    if (passwordChangeData.newPassword.length < 6) {
      setPasswordChangeError('New password must be at least 6 characters');
      setIsLoading(false);
      return;
    }

    const success = await changePassword(passwordChangeData.currentPassword, passwordChangeData.newPassword);
    setIsLoading(false);
    
    if (success) {
      setPasswordChangeSuccess(true);
      setPasswordChangeData({ currentPassword: '', newPassword: '', confirmPassword: '' });
      setTimeout(() => {
        setPasswordChangeSuccess(false);
        setShowPasswordChange(false);
      }, 2000);
    } else {
      setPasswordChangeError('Current password is incorrect');
    }
  };

  const handleInputChange = (e) => {
    setCredentials({
      ...credentials,
      [e.target.name]: e.target.value
    });
  };

  const handlePasswordChangeInputChange = (e) => {
    setPasswordChangeData({
      ...passwordChangeData,
      [e.target.name]: e.target.value
    });
  };

  return (
    <div className="login-overlay">
      <div className="login-modal">
        <div className="login-header">
          <h3>{showPasswordChange ? 'Change Password' : 'Admin Access Required'}</h3>
          <button className="close-btn" onClick={onClose}></button>
        </div>

        {!showPasswordChange ? (
          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <input
                type="email"
                id="email"
                name="email"
                value={credentials.email}
                onChange={handleInputChange}
                required
                autoComplete="email"
              />
            </div>

            <div className="form-group">
              <label htmlFor="password">Password</label>
              <input
                type="password"
                id="password"
                name="password"
                value={credentials.password}
                onChange={handleInputChange}
                required
                autoComplete="current-password"
              />
            </div>

            {error && <div className="error-message">{error}</div>}

            <div className="login-buttons">
              <button type="submit" className="login-btn" disabled={isLoading}>
                {isLoading ? 'Logging in...' : 'Login'}
              </button>
              <button 
                type="button" 
                className="change-password-btn"
                onClick={() => setShowPasswordChange(true)}
                disabled={isLoading}
              >
                Change Password
              </button>
            </div>
          </form>
        ) : (
          <form onSubmit={handlePasswordChange} className="password-change-form">
            <div className="form-group">
              <label htmlFor="currentPassword">Current Password</label>
              <input
                type="password"
                id="currentPassword"
                name="currentPassword"
                value={passwordChangeData.currentPassword}
                onChange={handlePasswordChangeInputChange}
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="newPassword">New Password</label>
              <input
                type="password"
                id="newPassword"
                name="newPassword"
                value={passwordChangeData.newPassword}
                onChange={handlePasswordChangeInputChange}
                required
                minLength="6"
              />
            </div>

            <div className="form-group">
              <label htmlFor="confirmPassword">Confirm New Password</label>
              <input
                type="password"
                id="confirmPassword"
                name="confirmPassword"
                value={passwordChangeData.confirmPassword}
                onChange={handlePasswordChangeInputChange}
                required
                minLength="6"
              />
            </div>

            {passwordChangeError && <div className="error-message">{passwordChangeError}</div>}
            {passwordChangeSuccess && <div className="success-message">Password changed successfully!</div>}

            <div className="login-buttons">
              <button type="submit" className="login-btn" disabled={isLoading}>
                {isLoading ? 'Changing...' : 'Change Password'}
              </button>
              <button 
                type="button" 
                className="cancel-btn"
                onClick={() => {
                  setShowPasswordChange(false);
                  setPasswordChangeError('');
                  setPasswordChangeSuccess(false);
                  setPasswordChangeData({ currentPassword: '', newPassword: '', confirmPassword: '' });
                }}
                disabled={isLoading}
              >
                Back to Login
              </button>
            </div>
          </form>
        )}
      </div>
    </div>
  );
}

export default Login;