// This is our main App component - the root of our application
// In React, components are like building blocks that return JSX (HTML-like syntax)

// Import React - needed for JSX
import React from 'react';

// Import our individual components
import Header from './components/Header';
import Hero from './components/Hero';
import About from './components/About';
import Timeline from './components/Timeline';
import Experience from './components/Experience';
import Projects from './components/Projects';
import Goals from './components/Goals';
import Contact from './components/Contact';

// Import authentication context
import { AuthProvider } from './contexts/AuthContext';

// Import our CSS file for styling
import './App.css';

// This is a functional component - the modern way to write React components
// It's a JavaScript function that returns JSX
function App() {
  return (
    <AuthProvider>
      {/* JSX must have a single parent element, so we wrap everything in a div */}
      {/* className is used instead of class (because class is a reserved word in JavaScript) */}
      <div className="App">
        {/* Each component is used like an HTML tag */}
        <Header />
        
        {/* Hero section - the main landing area */}
        <Hero />
        
        {/* main tag for semantic HTML - contains the main content */}
        <main>
          <About />
          <Timeline />
          <Experience />
          <Projects />
          <Goals />
          <Contact />
        </main>
        
        {/* Simple footer */}
        <footer className="footer">
          <p>&copy; 2025 Einar Carias. Built with React for learning purposes.</p>
        </footer>
      </div>
    </AuthProvider>
  );
}

// Export the component so it can be imported in other files
export default App;