import { useEffect, useRef, useState } from 'react';

export const useScrollAnimation = (threshold = 0.1, rootMargin = '0px 0px -50px 0px') => {
  const [isVisible, setIsVisible] = useState(false);
  const elementRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          // Once visible, stop observing to prevent re-triggering
          observer.unobserve(entry.target);
        }
      },
      {
        threshold,
        rootMargin,
      }
    );

    const currentElement = elementRef.current;
    if (currentElement) {
      observer.observe(currentElement);
    }

    return () => {
      if (currentElement) {
        observer.unobserve(currentElement);
      }
    };
  }, [threshold, rootMargin]);

  return [elementRef, isVisible];
};

// Parallax scroll hook for consistent parallax effects
export const useParallaxScroll = (speed = 0.5) => {
  const [offset, setOffset] = useState(0);
  const elementRef = useRef(null);

  useEffect(() => {
    const handleScroll = () => {
      if (elementRef.current) {
        const elementTop = elementRef.current.offsetTop;
        const elementHeight = elementRef.current.offsetHeight;
        const windowHeight = window.innerHeight;
        const scrollY = window.scrollY;
        
        // Calculate if element is in viewport
        const elementBottom = elementTop + elementHeight;
        const viewportTop = scrollY;
        const viewportBottom = scrollY + windowHeight;
        
        if (elementBottom >= viewportTop && elementTop <= viewportBottom) {
          // Element is in viewport, calculate parallax offset
          const parallaxOffset = (scrollY - elementTop) * speed;
          setOffset(parallaxOffset);
        }
      }
    };

    window.addEventListener('scroll', handleScroll);
    handleScroll(); // Initial calculation

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [speed]);

  return [elementRef, offset];
};

export const useScrollAnimationGroup = (threshold = 0.1, rootMargin = '0px 0px -50px 0px') => {
  const [visibleItems, setVisibleItems] = useState(new Set());
  const elementRefs = useRef([]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const index = elementRefs.current.indexOf(entry.target);
            if (index !== -1) {
              setVisibleItems(prev => new Set([...prev, index]));
              observer.unobserve(entry.target);
            }
          }
        });
      },
      {
        threshold,
        rootMargin,
      }
    );

    elementRefs.current.forEach((element) => {
      if (element) {
        observer.observe(element);
      }
    });

    return () => {
      elementRefs.current.forEach((element) => {
        if (element) {
          observer.unobserve(element);
        }
      });
    };
  }, [threshold, rootMargin]);

  const addRef = (index) => (el) => {
    elementRefs.current[index] = el;
  };

  const isVisible = (index) => visibleItems.has(index);

  return [addRef, isVisible];
};