// This is the entry point of our React application
// It's where React starts rendering our components

// Import React library - this is needed for JSX
import React from 'react';

// Import ReactDOM - this helps us render React components to the DOM
import ReactDOM from 'react-dom/client';

// Import our main App component
import App from './App';

// Import our CSS styles
import './index.css';

// Get the root element from our HTML file
// This is where our React app will be mounted
const root = ReactDOM.createRoot(document.getElementById('root'));

// Render our App component inside the root element
// React.StrictMode helps catch common mistakes during development
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);